@import "./mixins";

@font-face {
  font-family: 'montbold';
  src: url('./font/mont-bold-webfont.woff2'),
    url('./font/mont-bold-webfont.woff');
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'montregular';
  src: url('./font/mont-regular-webfont.woff2'),
    url('./font/mont-regular-webfont.woff');
  font-weight: 300;
  font-style: normal;

}

p {
  margin: 0;
  font-family: 'montregular';
}

h1,
h2 {
  font-family: 'montbold';
  margin: 0;
}

h3,
h4 {
  font-family: 'montregular';
  font-weight: 600;
  font-size: 20px;
  line-height: 26px;
  margin: 0;
}

h1 {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.01em;

  @include onTablet {
    font-size: 28px;
    line-height: 34px;
  }

  @include onDesktop {
    font-size: 32px;
    line-height: 41px;
  }
}

h2 {
  font-size: 30px;
  line-height: 40px;
}

h3 {
  font-size: 18px;
  line-height: 22px;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}
