.button {
  width: 32px;
  height: 32px;
  background-color: #fff;
  color: $color-primary;
  cursor: pointer;
  border: 1px solid #B4BDC4;

  &__arr {
    background-repeat: no-repeat;
    background-position: center;
    display: block;
    height: 100%;

    &--left {
      background-image: url(../img/Icons/arr-left.svg);
    }

    &--right {
      background-image: url(../img/Icons/arr-right.svg);
    }
  }
}
