* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: 'montregular';
}

body {
  margin: 0;
}

.slider {
  &__box {
    max-height: 400px;
    display: flex;
    gap: 16px;
    margin-bottom: 24px;
    width: 100%;
  }

  &__arrow {
    background-color: #fff;
    color: #fff;
    cursor: pointer;
    border: 1px solid #B4BDC4;

    &:active {
      background-color: #f4f6f8;
    }
  }

  &__arr {
    width: 10px;
    height: 10px;
    background-repeat: no-repeat;
    background-position: center;
    display: block;

    &--left {
      background-image: url(./img/Icons/arr-left.svg);
    }

    &--right {
      background-image: url(./img/Icons/arr-right.svg);
    }
  }

  &__dots {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
  }

  &__button-dots {
    background-color: white;
    cursor: pointer;
    border: 0px;
    width: 42px;
  }

  &__img-dots {
    display: block;
    width: 100%;
    cursor: pointer;
    width: 24px;
    height: 6px;

    &--active * {
      fill: #313237;
    }
  }

  &__main {
    width: 100%;
    height: 200px;
    overflow: hidden;

    @include onTablet {
      height: 300px;
    }

    @include onDesktopSmall {
      height: 400px;
    }
  }

  &__main {
    user-select: none;
    pointer-events: none;
  }

  &__list {
    height: 100%;
    display: flex;
  }

  &__img {
    object-fit: cover;
    object-position: center;
  }
}

.test {
  height: 200px;
  width: 100px;
  background-color: red;
}
