.item {
  &__root-box {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-bottom: 40px;
  }

  &__home-img {
    width: 24px;
    height: 24px;
  }

  &__arr-right {
    height: 12px;
    width: 12px;
  }

  &__root-name {
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    color: $color-secondary;
  }

  &__back-link {
    text-decoration: none;
    display: flex;
    gap: 10px;
  }

  &__back-arrow {
    width: 14px;
    height: 14px;
  }

  &__title {
    text-align: center;
    margin-bottom: 40px;

    @include onDesktopSmall {
      text-align: initial;
    }
  }

  &__back-link {
    margin-bottom: 16px;
  }

  &__main-block {
    display: block;
    gap: 75px;
    margin-bottom: 80px;

    @include onDesktopSmall {
      display: flex;
    }
  }

  &__images {
    display: flex;
    align-items: center;
    flex-direction: column-reverse;
    gap: 34px;
    margin-bottom: 40px;


    @include onDesktopSmall {
      flex-direction: row;
      margin-bottom: 0;
    }
  }

  &__left-block-img {
    display: flex;
    flex-direction: row;
    gap: 16px;

    @include onDesktopSmall {
      flex-direction: column;
    }
  }

  &__main-image {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 280px;
    height: 280px;

    @include onDesktopSmall {
      width: 464px;
      height: 464px;
    }
  }

  &__small-img-wrap {
    height: 40px;
    width: 40px;
    border: 1px solid#C4C4C4;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $color-white;
    transition: 0.3s linear;

    &:hover {
      box-shadow: 0 5px 5px -5px rgba(0, 0, 0, .5);
      border: 1px solid $color-primary;
    }

    &--active {
      border: 1px solid $color-primary;
    }

    @include onTablet {
      height: 60px;
      width: 60px;
    }

    @include onDesktopSmall {
      height: 80px;
      width: 80px;
    }
  }

  &__small-img {
    height: 33px;
    width: 100%;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;

    display: block;

    @include onTablet {
      height: 43px;
    }

    @include onDesktopSmall {
      height: 66px;
    }
  }

  &__big-image {
    height: 280px;
    display: block;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    width: 100%;
    transition: 0.1s linear;

    @include onDesktopSmall {
      height: 442px;
    }
  }

  &__general-block {
    width: 100%;
    display: flex;
    justify-content: center;

    @include onDesktopSmall {
      justify-content: space-between;
    }
  }

  &__choose-settings {
    width: 320px;
  }

  &__text {
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    color: $color-secondary;
  }

  &__colors-title {
    margin-bottom: 9px;
  }

  &__id-wrapper {
    display: flex;
    justify-content: flex-end;
  }

  &__id {
    display: none;
    font-weight: 600;
    width: 60px;
    font-size: 12px;
    line-height: 15px;
    color: #B4BDC4;

    @include onDesktopSmall {
      display: inherit;
    }
  }

  &__colors-list {
    display: flex;
    gap: 8px;
    padding-bottom: 24px;
    width: 100%;
    border-bottom: 1px solid #E2E6E9;
    margin-bottom: 24px;
  }

  &__color-wrapper {
    width: 36px;
    height: 36px;
    border: 1px solid #E2E6E9;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2px;

    &--active {
      border: 1px solid #313237;
    }
  }

  &__color {
    background-color: #FCDBC1;
    height: 100%;
    width: 100%;
    border-radius: 50%;
  }

  &__buttons-box {
    display: flex;
    gap: 8px;
    margin-bottom: 32px;
    padding-bottom: 24px;
    width: 100%;
    border-bottom: 1px solid #E2E6E9;
  }

  &__button {
    width: 64px;
    height: 32px;
    color: $color-primary;
    border: 1px solid #B4BDC4;
    background-color: #fff;
    cursor: pointer;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;

    &:active {
      background-color: #313237;
      color: #fff;
    }

    &--active {
      background-color: #313237;
      color: #fff;
    }
  }

  &__price-box {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 16px;
  }

  &__price {
    &--old {
      font-weight: 500;
      font-size: 22px;
      line-height: 28px;
      text-decoration-line: line-through;
      color: $color-secondary;
    }
  }

  &__buttons-add-box {
    width: 100%;
    display: flex;
    gap: 8px;
    margin-bottom: 32px;
  }

  &__button-buy {
    width: 100%;
    height: 48px;
    text-align: center;
    background-color: $color-primary;
    color: #fff;
    cursor: pointer;
    transition: 0.3s linear;

    &--active {
      color: $color-green;
      background-color: $color-white;
      border: 1px solid #E2E6E9;
    }
  }

  &__button-heart {
    height: 48px;
    width: 48px;
    cursor: pointer;
    border: 1px solid #B4BDC4;
    background-color: white;
    transition: 0.3s linear;

    &--active {
      border: 1px solid #E2E6E9;
    }
  }

  &__button-img {
    height: 100%;
    width: 100%;
    background-repeat: no-repeat;
    background-position: center;
    display: block;
    background-image: url(../img/Icons/heart.svg);
    transition: 0.3s linear;

    &--active {
      background-image: url(../img/Icons/Heart_filled.svg);
    }
  }

  &__desc {
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 16px;

    @include onDesktopSmall {
      justify-content: space-between;
    }
  }

  &__box-left,
  &__box-right {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }


  &__desc-title {
    color: $color-secondary;
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
  }

  &__desc-char {
    color: $color-primary;
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    text-align: right;
  }

  &__secondary-block {
    margin-bottom: 80px;
    display: block;
    gap: 75px;

    @include onDesktopSmall {
      display: flex
    }
  }

  &__secondary-left {
    max-width: 578px;
  }

  &__secondary-right {
    width: 80%;
  }

  &__secondary-title {
    padding-bottom: 16px;
    margin-bottom: 32px;
    width: 100%;
    border-bottom: 1px solid #E2E6E9;
  }

  &__general-info-list {
    display: flex;
    flex-direction: column;
    gap: 32px;
    margin-bottom: 80px;
  }

  &__general-info-title {
    margin-bottom: 16px;
    font-weight: 600;
    font-size: 20px;
    line-height: 26px;
    color: #313237;
  }

  &__general-text {
    color: #89939A;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
  }
}
